<template>
  <div class="container">
    <div class="main">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :loading-text="suctxt">
        <Banner />
        <Prolist />

        <div class="main_index">
          <!--  -->
          <van-list v-model="loading" :finished="finished" finished-text="" @load="onLoad">
            <ul>
              <li v-for="(item,index) of lists" :key="index">
                <div class="itemimg">
                  <div class="bigimg">
                    <img :src="item['thumbUrl']" alt />
                  </div>
                  <div class="smallimg">
                    <img src alt />
                  </div>
                </div>
                <p class="itemtit">{{item['alias']}}</p>
                <p class="itemliang">{{item['productName']}}</p>
                <p class="itemscall">
                  <span class="redfont">{{item['matchPrice']}}</span>
                  <span class="oldfont">{{item['marketPrice']}}</span>
                  <span
                    class="iconfont icon-tubiaolunkuo-"
                    id="icon"
                    @click="sendState(item['thumbUrl'],item['productName'],item['matchPrice'],item['productId'],$event,flag=true)"
                  ></span>
                </p>
              </li>
            </ul>
          </van-list>
        </div>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { List, PullRefresh, Toast } from 'vant'
Vue.use(PullRefresh)
Vue.use(List)
Vue.use(Toast)

// import { log } from 'util'
import Banner from '@/components/home/banner'
import Prolist from '@/components/home/prolist'
import Lists from '@/components/common/lists'
// import List from '@/components/home/pro/list'
// Vue.user(log)

export default {
  components: {
    Banner,
    Prolist
  },
  data () {
    return {
      lists: '',
      loading: false,
      finished: false,
      pageNum: 0,
      isLoading: false,
      suctxt: '',
      obj: {},
      lis: [],
      userinfo: this.$store.state.userInfo,
      liss:''
    }
  },
  methods: {
    sendState (pic, name, sale, proid, enent, flag) {
      event.stopPropagation()
      if (this.$store.state.loginState === "ok") {
        // this.$store.commit('addBuycart', {
        //   result: {pic, name, sale, proid,flag}
        // })
        let result = { pic, name, sale, proid, flag }
        if (!localStorage.getItem("cars")) {
          if (this.obj[this.userinfo] === undefined) {
            this.obj[this.userinfo] = this.lis
          }
        } else {
          let dic = JSON.parse(localStorage.getItem("cars"))
          if (dic[this.userinfo] !== undefined) {
            this.lis = dic[this.userinfo]
          }
        }
        if (!localStorage.getItem("cars")) {
          this.lis.push(result);
          // let newArr = []
          // let newsArr = []
          // for (let i = 0; i < this.lis.length; i++) {
          //   newsArr.push(this.lis[i].proid)
          //   let flag = true
          //   for (let a = 0; a < newArr.length; a++) {
          //     if (this.lis[i].proid == newArr[a].proid) {
          //       flag = false
          //     }
          //   }
          //   if (flag) {
          //     newArr.push(this.lis[i])
          //   }
          // }
          // let a = newsArr.reduce(function (prev, next) {
          //   prev[next] = (prev[next] + 1) || 1;
          //   return prev;
          // }, {})
          // for (let p = 0; p < newArr.length; p++) {
          //   for (let i in a) {
          //     if (newArr[p].proid == Number(i)) {
          //       newArr[p].num = a[i] // , 2key3
          //     }
          //   }
          // }
          // this.liss = newArr
          this.obj[this.userinfo] = this.lis;
          let dobj = JSON.stringify(this.obj)
          localStorage.setItem("cars", dobj)
        } else {
          let dic = JSON.parse(localStorage.getItem("cars"))
          this.lis.push(result);
          // let newArr = []
          // let newsArr = []
          // for (let i = 0; i < this.lis.length; i++) {
          //   newsArr.push(this.lis[i].proid)
          //   let flag = true
          //   for (let a = 0; a < newArr.length; a++) {
          //     if (this.lis[i].proid == newArr[a].proid) {
          //       flag = false
          //     }
          //   }
          //   if (flag) {
          //     newArr.push(this.lis[i])
          //   }
          // }
          // let a = newsArr.reduce(function (prev, next) {
          //   prev[next] = (prev[next] + 1) || 1;
          //   return prev;
          // }, {})
          // for (let p = 0; p < newArr.length; p++) {
          //   for (let i in a) {
          //     if (newArr[p].proid == Number(i)) {
          //       newArr[p].num = a[i] // , 2key3
          //     }
          //   }
          // }
          // this.lis = newArr
          dic[this.userinfo] = this.lis;
          let dobj = JSON.stringify(dic);
          localStorage.setItem("cars", dobj)
          console.log(this.lis.length);

        }
        Toast('')
      } else {
        this.$router.push('/login')
      }
    },
    onLoad () {
      this.loading = true // loading
      this.pageNum++
      fetch('http://localhost:8000/cplist?count=5&pageNum=' + this.pageNum).then(res => res.json()).then(data => {
        this.loading = false
        if (data.length === 0) { // 3finishedtrue
          console.log('');
          this.finished = true
        } else { // 
          this.lists = [...this.lists, ...data]
        }
      })
    },
    onRefresh () {
      this.isLoading = true;
      this.pageNum = 0;
      fetch('http://localhost:8000/cplist?count=5&pageNum=1').then(res => res.json()).then(data => {
        this.isLoading = false;
        this.lists = data;
        this.finished = false;
      })
    }
  },
  mounted () {
    const main = document.querySelector('.main') // 
    main.addEventListener('scroll', this.scrollFn) // 
  },
  beforeRouteLeave (to, from, next) {
    const main = document.querySelector('.main')
    main.removeEventListener('scroll', this.scrollFn)
    localStorage.setItem('position', main.scrollTop)
    next()
  },
  watch: {
    $route (newVal, oldVal) {
      if (newVal.name === 'home') {
        const mains = document.querySelector('.main')
        mains.scrollTop = localStorage.getItem('position')
      }
    }
  }
}
</script>

<style lang="scss">
.itemscall {
  position: relative;
  .oldfont {
    padding: 0 8px;
    padding: 0.01rem 0.08rem 0;
    font-size: 0.12rem;
    color: #999;
    text-decoration: line-through;
  }
  #icon {
    font-size: 24px;
    position: absolute;
    color: green;
    top: -6px;
    right: 0.1rem;
    bottom: -0.05rem;
  }
  .redfont {
    color: #ee2e52;
    padding: 0.01rem 0.08rem 0;
    font-size: 13px;
    margin: 0 4px 0 0;
  }
}

.main_index {
  width: 100%;
  height: 100%;
  ul {
    width: 100%;
    height: 100%;
    display: flex;
    background-color: #f8f8f8;
    // flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    li {
      width: 1.57rem;
      height: 2.17rem;
      background-color: #fff;
      margin-top: 0.06rem;
      .itemimg {
        width: 100%;
        height: 1.57rem;
        position: relative;
        .bigimg {
          width: 100%;
          height: 1.57rem;
          background-color: #333;
          img {
            width: 100%;
            height: 1.57rem;
          }
        }
        .smallimg {
          width: 0.39rem;
          height: 0.39rem;
          position: absolute;
          left: 0;
          top: 0;
          padding: 0.01rem;
          img {
            width: 0.39rem;
            height: 0.39rem;
          }
        }
      }
      .itemtit {
        padding: 0.01rem 0.08rem 0;
        font-size: 12px;
      }
      .itemliang {
        padding: 0.01rem 0.08rem 0;
        font-size: 8px;
        color: #777;
      }
    }
  }
}
</style>

